import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { Observable } from 'rxjs';
import { catchError, map, tap } from 'rxjs/operators';

import { Article } from './Article';
import { shallowArticle } from './shallowArticle';


const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

@Injectable()
export class ArticleService {

  private articleServiceUrl = 'http://klein.eastmanhome.com';

  constructor(private http: HttpClient) { }

  getShallowArticleList(): Observable<shallowArticle[]> {
    return this.http.get<shallowArticle[]>(this.articleServiceUrl + '/api/articles/shallow');
  }

  getArticleList(): Observable<Article[]> {
    return this.http.get<Article[]>(this.articleServiceUrl + '/api/2011/12/articles');
  }

}





